
        /* NAV CSS */      
        nav {
            background-color: #fff;
            height: 80px;
            width: 100%;
            border-radius: 10px;
            margin-top: 20px;
        }

        label.logo {
            color: black;
            font-size: 35px;
            line-height: 80px;
            padding: 0 100px;
            font-weight: bold;
        }

        nav ul {
            float: right;
            margin-right: 60px;
        }

        nav ul li {
            display: inline-block;
            line-height: 80px;
            margin: 0 30px;
        }

        nav ul li a {
            color: black;
            font-size: 17px;
            text-transform: uppercase;
            text-decoration: none;
        }
        
        .checkbtn {
            font-size: 30px;
            color: black;
            float: right;
            line-height: 80px;
            margin-right: 40px;
            cursor: pointer;
            display: none;
        }

        #check {
            display: none;
        }

        @media (max-width: 952px) {
            label.logo{
                font-size: 30px;
                padding-left: 30px;
            }

            nav ul li a {
                font-size: 16px;
            }
        }
        @media (max-width: 858px) {
            .checkbtn {
                display: block;
            }

            ul {
                position: fixed;
                width: 100%;
                height: 40pvh;
                background-color: #fff;
                top: 80px;
                left: -100%;
                text-align: center;
                transition: all .5s;
            }

            nav ul li {
                display: block;
                margin: 50px 0;
                line-height: 30px;
            }

            nav ul li a {
                font-size: 20px;
            }
            a:hover,a.active {
                background: none;
                color: #000; 
            }
            #check:checked ~ ul {
                left: -35px;
            }
        }